.modal {
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.5);
  }
  
  .modal-content {
    background-color: #fefefe;
    margin: 15% auto;
    border: 1px solid #888;
    width: 350px;
    height: 250px;
    position: relative;
  }
  
  .box_s{
    border: 1px solid rgb(95, 25, 25);
    border-radius: 3px;
    text-align: left;
    margin: 20px;
  }

  .line_s {
    border-bottom: 1px solid rgb(95, 25, 25);
  }

  .pad {
    padding: 10px 20px;
  }

  .close:hover,
  .close:focus,
  .copybtn:hover,
  .copybtn:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
  }

  .atitle {
    padding-top: 20px;
    font-weight: bold;
    margin-bottom: 10px;
    font-size: large;
  }

  .abtns {
    width: 350px;
    position: absolute;
    bottom: 0;
  }

  .abtns button {
    width: 175px;
    border: none;
    padding: 15px 0;
  }

  .copybtn {
    background-color:rgb(236, 199, 199);
  }