.calendar {
    background-color: white;
  }

  .cal {
    display: flex;
    flex-direction: column;
    max-width: 300px;
    margin: 0 auto;
  }
  
  .weekdays {
    display: flex;
    justify-content: space-between;
    font-weight: bold;
    margin-top: 15px;
    margin-bottom: 10px;
    margin-left: 10px;
  }
  
  .days {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    gap: 10px;
  }
  
  .day {
    padding: 9px;
    text-align: center;
    position: relative; /* 상대 위치 설정 */
  }
  
  
  .day.heart {
    position: relative;
    font-weight: bold;
  }
  
  .day.heart::after {
    content: '\2665'; /* 유니코드 하트 문자 */
    font-size: 40px;
    color: #ff56226c;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  
  .red {
    color: red;
  }
  
  .blue {
    color: blue;
  }

  .remain {
    margin-top: 20px;
    border: 1px dotted rgb(95, 25, 25);
    padding: 20px 15px;
  }

  .remainDay {
    font-weight: bold;
  }