/* 갤러리 */
.image-gallery-content {
  width: 380px;
}

.gallery {
    background-color: #fdf7f7;
  }
  
  .gallery-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 5px;
    max-width: 350px;
    margin: 30px auto;
  }
  
  .gallery-item {
    overflow: hidden;
    border-radius: 3px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    cursor: pointer;
  }
  
  .photo {
    width: 100%;
    height: auto;
    display: block;
  }
  
  .g-modal {
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  /* 스와이프 동작 */
  .g-modal-content {
    display: block;
    width: 80%;
    max-width: 700px;
    touch-action: pan-y; /* 수직 스와이프만 가능하도록 설정 */
  }
  
  .g-close {
    position: absolute;
    top: 15px;
    right: 35px;
    color: #fff;
    font-size: xx-large;
    font-weight: bold;
    cursor: pointer;
  }
  
  .g-btn {
    cursor: pointer;
    margin: 2px;
    padding: 5px;
    color: #fff;
    font-size: x-large;
    font-weight: bold;
    user-select: none; /* 사용자 선택 방지 */
    touch-action: manipulation; /* 터치 동작 지정 */
  }

 